<template>
  <div class="result">
    <div v-if="isShow">默认内容</div>
    <div v-else>搜索结果:{{content}}</div>
  </div>

</template>

<script>
import { getChannelArticleByContent } from "@/request/api.js";
export default {
  name: "Result",
  components: {},
  props: {
    searchText: {
      type: String,
      required: true,
    },
    isShow: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      content: "",
    };
  },
  computed: {},
  watch: {
    // 当v-if为false时，组件完全未被渲染，子组件内部watch也就根本不会执行
    // 当v-if变成true时，数据第一次传过来，默认是不会触发监听函数的，如果这个时候想要监听第一次刚传过来的值，使用immediate
    isShow: {
      immediate: true, // 立即监听，即第一次传递数据过来时也触发一次监听函数
      handler() {
        if (this.isShow == false) {
          this.getContent();
        }
      },
    },
  },
  created() {
    console.log("nihao");
  },
  methods: {
    async getContent() {
      let { data } = await getChannelArticleByContent(this.searchText);
      console.log(data.data.content);
      this.content = data.data.content;
    },
  },
};
</script>
