<template>
  <div class="search">
    <!-- 搜索栏 -->
    <form class="searchBox" action="/">
      <van-search v-model="searchText" background="#3296fa" placeholder="请输入搜索关键词" shape="round" show-action @search="onSearch" @cancel="$router.back()" @focus="isResultShow = false" />
      <!-- @search确定搜索时触发，@focus输入框获得焦点时触发,即当点击搜索框获取焦点时，就让搜索结果为false -->
    </form>

    <div class="blank"></div>
    <!-- 1 默认显示搜索结果/或者默认显示的推荐内容 -->
    <Result ref="resultRef" v-if="isResultShow" :searchText="searchText" :isShow='isShow'></Result>

    <!-- 3 联想建议，当有输入内容时就会显示联想建议 -->
    <Suggestion v-else-if="searchText" :searchText="searchText" @search="onSearch" />
    <!-- 把输入的内容传递给子组件；子组件可以触发父组件的搜索时间 -->

    <!-- 2 历史记录，当搜索框获得焦点时，搜索结果为false，同时没有输入搜索内容，则联想建议也是false，此时前两个都是false，则显示历史记录 -->
    <History v-else :searchHistories="searchHistories" @search="onSearch" />
    <!-- 所以整个逻辑就是，一进入页面首先显示默认内容，然后点击搜索框，显示历史记录，之后输入搜索内容，显示搜索联系建议 -->
  </div>
</template>

<script>
import Result from "./Result.vue";
import Suggestion from "./Suggestion.vue";
import History from "./History.vue";
export default {
  name: "Search",
  components: { Result, Suggestion, History },
  props: {},
  data() {
    return {
      searchText: "", // 搜索框输入的内容
      isResultShow: true, // 搜索结果的显示状态
      suggestions: [], // 联想建议
      searchHistories: [],
      isShow: true,
    };
  },
  computed: {},
  watch: {},
  created() {},
  methods: {
    onSearch(searchText) {
      // 1. 修改搜索框的文本内容
      this.searchText = searchText;
      // 2. 记录搜索历史记录
      const index = this.searchHistories.indexOf(this.searchText);
      if (index == -1) {
        this.searchHistories.unshift(this.searchText);
      }
      // 3. 展示搜索结果
      this.isResultShow = true;
      this.isShow = false; // 在result搜索内容页面中关闭默认内容，显示搜索内容
    },
  },
};
</script>
<style lang='less' scoped>
.searchBox {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}
.van-search__action {
  color: #fff;
}
.van-search {
  height: 60px;
}
.blank {
  height: 60px;
}
</style>