<template>
  <div class="suggestion">
    <van-cell-group>
      <!-- 点击建议联想触发搜索时间: 把建议联想的内容作为参数传调用父组件的search方法 -->
      <van-cell icon="search" v-for="item in suggestions" :key="item._id" :title="item.content" @click="$emit('search', item.content)" />
    </van-cell-group>
  </div>
</template>

<script>
import debounce from "lodash/debounce"; // 这个其实也可以使用cdn导入
import { getSearchSuggest } from "@/request/api.js";
export default {
  name: "Suggestion",
  components: {},
  props: {
    searchText: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      suggestions: [],
    };
  },
  computed: {},
  watch: {
    // 监听输入内容的变化
    searchText: {
      // 防止输入过快，请求过于频繁，进行防抖处理，而且本来也没必要没每输入一个字符就建议一次，应该就是输入几个字符才联想建议一次
      handler: debounce(async function () {
        if (!this.searchText) {
          return;
        }
        // 获取百度api搜索联想建议，返回的是jsonp格式，需要转换成js对象格式才能用
        // const { data } = await axios.get("/searchApi/su", {
        //   params: { wd: this.searchText },
        // });
        try {
          const { data } = await getSearchSuggest(this.searchText);
          this.suggestions = data.data;
          console.log(this.suggestions);
        } catch (err) {
          console.log(err);
        }
      }, 200),
      immediate: true, // 在监听开始时就立即调用一次函数
      // 当父组件向子组件传值时，子组件props首次获取到父组件传来的默认值时，此时数据第一次初始化，并不算产生变化，所以默认不会执行函数
      // 如果在第一次数据初始化时也要执行函数，此时就要将immediate设为true，同时要使用handler，后面跟处理函数
    },
  },
  created() {},
  methods: {},
};
</script>
<style lang='less' scoped>
</style>